declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { errorMessage } from 'src/app/messages';
import { PaginationService } from 'src/app/service/pagination.service';
import { ProductService } from 'src/app/service/product.service';
import { DataService } from 'src/app/service/data.service';
import { ISubscription } from "rxjs/Subscription";
import { DropDownService } from 'src/app/service/drop-down.service';

@Component({
  selector: 'app-offer-product-list',
  templateUrl: './offer-product-list.component.html'
})
export class OfferProductListComponent implements OnInit {

  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  private subscription: ISubscription;
  categoryList: any;
  category_id: string = null;
  brandList: any = [];
  brand_id: string = null;

  constructor(
    private router: Router,
    private productService: ProductService,
    private dataService: DataService,
    private dropdownService: DropDownService,
    private paginationService: PaginationService
  ) {
  }

  ngOnInit() {
    this.subscription = this.dataService.searchTextSubject.subscribe((v) => {
      this.searchText = v;
      this.getCategoryList()
      this.getObjects();
    });
  }

  getCategoryList() {
    this.dropdownService.getCategoryDrp().subscribe((res) => {
      if (res.success) {
        this.categoryList = res.data ? res.data : [];
      }
    });
  }

  getBrandList() {
    const params: any = {};
    if (this.category_id) {
      params.category_id = this.category_id;
    }
    this.dropdownService.getBrandDrp(params).subscribe((res) => {
      if (res.success) {
        this.brandList = res.data ? res.data : [];
      }
    });
  }


  onCategoryChange() {
    this.getBrandList();
    this.getObjects();
  }

  getjson(data) {
    if (data)
      return JSON.parse(data);
  }

  getObjects() {
    this.loadingState = true;
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    if (this.category_id) {
      params.category_id = this.category_id;
    }
    if (this.brand_id) {
      params.brand_id = this.brand_id;
    }
    this.productService.getOfferProductList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.data;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
      // console.log(error);
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.getObjects();
  }

  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.productService.deleteOfferProduct(object.id).subscribe(
          (response) => {
            if (response.success) {
              this.getObjects();
            }
          },
        );
      }
    }).catch(swal.noop);
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

}

