import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { OfferProductListComponent } from './offer-product-list.component';
import { ProductService } from 'src/app/service/product.service';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { FormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: '',
    component: OfferProductListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../offer-product-add/offer-product-add.module').then(m => m.OfferProductAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../offer-product-add/offer-product-add.module').then(m => m.OfferProductAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    OfferProductListComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    PaginationModule,
    LoderModule,
    NgSelectModule,
    FormsModule
  ],
  providers: [
    ProductService
  ]
})

export class OfferProductListModule { }
